<template>
  <div class="orderConfirm">
    <!-- 顶部导航栏 -->
    <van-sticky :offset-top="0">
      <van-nav-bar
        title="订单确认"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
      />
    </van-sticky>
    <!-- 整体背景 -->
    <div class="bg">
      <van-cell size="large" title="服务地址" icon="location-o">
      </van-cell>
      <van-cell size="large" title="订单详情" icon="orders-o" >
        <template #label>
          <van-row v-for="line of orderLines.values()" :key="line.productId">
            <van-col span="8">{{line.productName}}</van-col>
            <van-col span="8">￥{{line.price}}</van-col>
            <van-col span="8">x{{line.number}}</van-col>
          </van-row>
          <van-row style="border-top:1px dotted #ededed">
            <van-col span="12"></van-col>
            <van-col span="12">总计:￥{{total}}</van-col>
          </van-row>
        </template>
      </van-cell>
      <van-cell @click="()=>{this.show = true}" :value="timeValue | dateFormat" size="large" title="服务时间" icon="underway-o">
      </van-cell>
      <van-cell size="large" title="支付方式" icon="cash-on-deliver" />
    </div>
    <!-- 日期选择器 -->
    <van-popup v-model="show" position="bottom">
      <van-datetime-picker
        v-model="currentDate"
        type="datetime"
        :columns-order="['year','month','day','hour','minute']"
        @confirm="confirmDate(currentDate)"
        @cancel="cancelDate"
        :formatter="formatter"
      />
    </van-popup>
    <!-- 提交按钮 -->
    <div class="confirm_btn" @click="confirm">确认提交</div>
  </div>
</template>

<script>
import {mapState,mapActions,mapGetters} from 'vuex'
export default {
  data() {
    return {
      // 选择的时间
      timeValue: new Date(),
      // 当前时间
      currentDate:new Date(),
      // 日期选择器显示控制
      show:false
    }
  },
  computed:{
    // 获取保存的订单信息
    ...mapState('shopcart',['orderLines']),
    // 获取订单总价
    ...mapGetters('shopcart',['total']),
  },
  methods: {
    // 时间确认
    confirmDate(value){
      // 时间赋值
      this.timeValue = value
      // 关闭时间选择
      this.show = false
    },
    cancelDate(){
      this.show = false
    },
    // 日期选择器格式
    formatter(type, val) {
      if (type === 'year') {
        return val + '年';
      }
      if (type === 'month') {
        return val + '月';
      }
      if (type === 'day') {
        return val + '日';
      }
      if (type === 'hour') {
        return val + '时';
      }
      if (type === 'minute') {
        return val + '分';
      }
      return val;
    },
    // 提交订单
    confirm(){

    },
    // 返回上一级页面
    onClickLeft(){
      this.$router.go(-1)
    },
  },
};
</script>

